@import url('https://fonts.googleapis.com/css2?family=Bungee+Inline&family=Chicle&display=swap');

.bungee-inline-regular {
  font-family: "Bungee Inline", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.chicle-regular {
  font-family: "Chicle", serif;
  font-weight: 400;
  font-style: normal;
}

html, body, #root {
  width: 100%;
  max-width: 100vw;
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  background: #fff;
  color: #333;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: normal;
  font-family: 'Chicle', sans-serif;
}

.tierra-alma-container {
  padding-top: 8rem;
  padding-bottom: 4rem;

  h1, h2, h3, h4, h5, h6 {
    font-family: 'Bungee Inline', sans-serif;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.bg-gradient-brown {
  background: linear-gradient(90deg, #78350f 0%, #b45309 100%);
  color: #fff;
}

.bg-gradient-brown-light {
  background: linear-gradient(90deg, #92400e 0%, #f59e42 100%);
  color: #fff;
}

.bg-amber-50 {
  background: #fef3c7;
}

.bg-amber-700 {
  background: #b45309;
}

.bg-amber-800 {
  background: #92400e;
}

.text-amber-900 {
  color: #78350f;
}

.text-amber-800 {
  color: #92400e;
}

.text-amber-700 {
  color: #b45309;
}

.text-green-200 {
  color: #bbf7d0;
}

.text-red-200 {
  color: #fecaca;
}

.rounded-lg {
  border-radius: 0.75rem;
}

.rounded-md {
  border-radius: 0.375rem;
}

.shadow-md {
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}

.shadow-lg {
  box-shadow: 0 10px 15px rgba(0,0,0,0.1);
}

.shadow-xl {
  box-shadow: 0 20px 25px rgba(0,0,0,0.1);
}

.font-bold {
  font-weight: bold;
}

.font-medium {
  font-weight: 500;
}

.text-center {
  text-align: center;
}

.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

.mb-4 { margin-bottom: 1rem; }
.mb-6 { margin-bottom: 1.5rem; }
.mb-8 { margin-bottom: 2rem; }
.mb-10 { margin-bottom: 2.5rem; }
.mt-4 { margin-top: 1rem; }
.mt-6 { margin-top: 1.5rem; }

.p-6 { padding: 1.5rem; }
.py-16 { padding-top: 4rem; padding-bottom: 4rem; }
.px-6 { padding-left: 1.5rem; padding-right: 1.5rem; }
.py-24 { padding-top: 6rem; padding-bottom: 6rem; }
.py-32 { padding-top: 8rem; padding-bottom: 8rem; }

.flex { display: flex; }
.items-center { align-items: center; }
.justify-center { justify-content: center; }
.flex-col { flex-direction: column; }
.gap-3 { gap: 0.75rem; }
.gap-6 { gap: 1.5rem; }
.gap-8 { gap: 2rem; }
.grid { display: grid; }
.grid-cols-1 { grid-template-columns: 1fr; }
.sm\:grid-cols-2 { grid-template-columns: repeat(2, 1fr); }
.lg\:grid-cols-3 { grid-template-columns: repeat(3, 1fr); }
.md\:grid-cols-2 { grid-template-columns: repeat(2, 1fr); }
.md\:grid-cols-3 { grid-template-columns: repeat(3, 1fr); }

@media (min-width: 640px) {
  .sm\:flex-row { flex-direction: row; }
  .sm\:grid-cols-2 { grid-template-columns: repeat(2, 1fr); }
}
@media (min-width: 768px) {
  .md\:text-4xl { font-size: 2.25rem; }
  .md\:text-5xl { font-size: 3rem; }
  .md\:text-xl { font-size: 1.25rem; }
  .md\:py-32 { padding-top: 8rem; padding-bottom: 8rem; }
  .md\:grid-cols-2 { grid-template-columns: repeat(2, 1fr); }
  .md\:grid-cols-3 { grid-template-columns: repeat(3, 1fr); }
  .md\:order-1 { order: 1; }
  .md\:order-2 { order: 2; }
}
@media (min-width: 1024px) {
  .lg\:text-6xl { font-size: 3.75rem; }
  .lg\:grid-cols-3 { grid-template-columns: repeat(3, 1fr); }
}

input, button {
  font-size: 1rem;
}

input[type="email"] {
  padding: 0.75rem 1rem;
  border-radius: 0.375rem;
  border: 1px solid #e5e7eb;
  outline: none;
  width: 100%;
}

input[type="email"]:focus {
  border-color: #f59e42;
  box-shadow: 0 0 0 2px #fde68a;
}

button {
  padding: 0.75rem 1.5rem;
  border-radius: 0.375rem;
  border: none;
  background: #b45309;
  color: #fff;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s;
}

button:hover {
  background: #92400e;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}
